.skeleton {
  height: 300px;
  width: 30%;
  background: rgb(248, 247, 247);
  padding: 10px;
  margin: 15px;
  border-radius: 0px;
}

.skeletonTop {
  display: flex;
  align-items: center;
  overflow: hidden;
  position: relative;
}

.skeletonImg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
  margin-left: 10px;
  background: #ddd;
  overflow: hidden;
  position: relative;
}

.skeletonName {
  width: 100px;
  height: 10px;
  position: relative;
  background: #ddd;
  overflow: hidden;
}

.skeletonTainer {
  padding: 10px;
  position: relative;
}

.skeletonText {
  width: 200px;
  height: 10px;
  background: #ddd;
  margin-top: 10px;
  position: relative;
  overflow: hidden;
}

.skeletonContainer {
  width: 100%;
  height: 180px;
  background: #ddd;
  margin-top: 10px;
  position: relative;
  overflow: hidden;
}

.loading {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  animation: zedmiles 2s infinite;
}

.load {
  width: 50%;
  height: 100%;
  background: rgba(194, 189, 189, 0.159);
  transform: skewX(-20deg);
}

@keyframes zedmiles {
  0% {
    transform: translateX(-150%);
  }
  50% {
    transform: translateX(-80%);
  }
  100% {
    transform: translateX(100%);
  }
}

@media (max-width: 800px) {
  .skeleton {
    height: 300px;
    width: 90%;
    background: rgb(248, 247, 247);
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  }
}
